html, body, #root {
  height: 100%;
  margin: 0;
  overflow: hidden;
}

.app-container {
  flex: 1;
  height: 100%;
  overflow-y: auto;
  padding: 20px;
  box-sizing: border-box;
  font-family: Arial, sans-serif;
  text-align: center;
}

.content-wrapper {
  max-width: 700px;
  margin: 0 auto;
}

.response-box {
  margin-top: 20px;
  padding: 16px;
  background-color: #f8f8f8;
  border-left: 4px solid #4caf50;
  border-radius: 8px;
  font-family: Consolas, monospace;
  font-size: 14px;
  white-space: pre-wrap;
  word-break: break-word;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
  max-width: 7000px;
}

.response-box h2 {
  margin-top: 0;
  color: #333;
}

.response-box pre {
  margin: 0;
  color: #444;
  max-width: 1000px;
  white-space: pre-wrap;
  overflow-wrap: break-word; /* allows breaking inside long strings */
  word-break: break-word;    /* forces breaking within long words if needed */
}

.prompt-box {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border-radius: 8px;
  border: 1px solid #ccc;
  resize: vertical;
  box-sizing: border-box;
}

button {
  margin-top: 10px;
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  border-radius: 6px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

.main-content {
    display: flex;
    height: 100vh; /* Full screen height */
}

.sticky-bar-wrapper {
    position: sticky;
    top: -20px;
    z-index: 1000;
    background-color: white;
    padding: 1rem 0;
    max-width: 700px;
    margin: 0 auto;

}
